@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  overflow-x: hidden;
}
.Home {
  width: 100%;
  height: 100%;
  position: absolute;
}
.Flexbox {
  display: flex;
  flex-direction: row;
  height: 100%;
}
.Content-container {
  height: 100%;
}
.Text-container {
  font-family: 'Zilla Slab', serif;
  margin: 60px;
  white-space: nowrap;
}

.Text-container h1 {
  font-size: 100px;
  font-weight: bold;
  line-height: 80px;
  margin: 10px;
}
.Text-container p, h3 {
  font-size: 50px;
  font-weight: normal;
  margin: 10px;
}
.Social {
  display: flex;
  position: absolute;
  bottom: 30px;
}
.Social-icon {
  width: 30px;
  height: 30px;
  margin: 20px;
  transition-duration: 1s;
}
.Social-icon:hover {
  color: #61DAFB;
  transition-duration: 1s;
}

/*Left content*/
.Left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  background-color: #ffffff;
  color: #282c34; 
}
.Left h1 {
  margin-left: 0px;
}
.Linkedin {
  width: 50%;
  justify-content: flex-end;
}
.Linkedin-icon {
  display: block;
  color: #282c34;
  transition-duration: 1s;
}
.Linkedin-icon-dark {
  display: none;
  color: #ECECEC;
}

/*Left animation*/
.Left-animation {
  position: absolute;
  height: 450px;
  width: 350px;
}
.More-left {
  display: block;
  position: absolute;
  transform: rotateY(180deg);
  margin-top: 110%;
  margin-left: -40%;
  opacity: 0;
  width: 40px;
  height: 40px;
  color: #282c34;
  transition-duration: 1s;
}
.More-left:hover {
  color: #61DAFB;
  transition-duration: 1s;
}
.More-left-dark {
  display: none;
  position: absolute;
  margin-top: 110%;
  margin-left: -40%;
  opacity: 0;
  width: 40px;
  height: 40px;
  color: #ECECEC;
  transition-duration: 1s;
}
.More-left-dark:hover {
  color: #61DAFB;
  transition-duration: 1s;
}

/*Right*/
.Right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  background-color: #282c34;
  color: #ECECEC;
}
.Dark-mode-icon {
  width: 30px;
  height: 30px;
  margin: 20px;
  color: #ECECEC;
}
.Dark-mode {
  z-index: 10;
  position: absolute;
  right: 0px;
  top: 0px;
}
#Dark-on {
  display: block;
}
#Dark-off {
  display: none;
}
.Github {
  width: 50%;
}
.Github a {
  margin-bottom: -4px;
}
.Github-icon {
  color: #ECECEC;
  transition-duration: 1s;
}

/*Right animation*/
.Right-animation {
  position: absolute;
  height: 450px;
  width: 350px;
  right: 0px;
}
.More-right{
  display: block;
  position: absolute;
  transform: rotateY(180deg);
  margin-top: 110%;
  margin-left: 72%;
  opacity: 0;
  transition-duration: 1s;
  width: 40px;
  height: 40px;
  z-index: 300;
  color: #ECECEC;
}
.More-right:hover {
  opacity: 1;
  color: #61DAFB;
  transition-duration: 1s;
}
.Suport-right {
  display: none;
  position: absolute;
  width: 600px;
  height: 300px;
  margin-top: 70%;
  margin-left: -110%;
}
.About{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
#Header {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: fixed;
  width: 100%;
  margin: 10px;
}
.Main-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 15px;
}
.Menu-icon {
  width: 40px;
  height: 40px;
  color: #282C34;
  transition-duration: 1s;
}
.Menu-icon:hover {
  color: #61DAFB;
  transition-duration: 1s;
}
.Photo {
  width: 250px;
  height: 250px;
}
.Social-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  width: 100%;
}
.Icons-area {
  display: flex;
  flex-direction: row;
  margin-top: 20px;
}
.Icons {
  width: 50px;
  height: 50px;
  margin: 20px 70px 35px 70px;
  color: #282c34;
  transition-duration: 1s;
}
.Icons:hover {
  color: #61DAFB;
  transition-duration: 1s;
}
.Number-social {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Number {
  font-size: 40px;
  font-weight: bold;
  margin-top: 0px;
}
#Body {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.Title-body {
  font-weight: bold;
  margin-top: 100px;
}
.About-body {
  display: flex;
  flex-direction: row;
  text-align: right;
  width: 55%;
  margin-top: 80px;
}
#Body p {
  font-size: 25px;
  font-family: 'Zilla Slab', serif;
  color: #282c34;
  
}
#Body span {
  font-size: 35px;
}
.Photo-about {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 20px;
}
.Services {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 70%;
}
.Services-item {
  display: flex;
  flex-direction: column;
  width: 300px;
  text-align: left;
  margin: 10px;
}
.Services-item p{
  font-size: 18px !important;
  margin: 5px;
}
.Item-flex {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.Item-flex p{
  font-weight: bold;
  font-size: 20px !important; 
  margin: 0px;
}
.Icon-service {
  width: 25px;
  height: 25px;
  margin: 10px;
}
#Footer {
  width: 100%;
  height: 50px;
  margin-top: 30px;
  bottom: 0px;
  background-color: #282c34;
}
